import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"
import { getIncomeStatementFiscalPeriod } from "../../utils/fiscalPeriodUtils"

function costAndExpenses(tableName: string) {
  const title = "Cost And Expenses (FQ)"
  return genericColumn({
    endpoint: "companyIncomeStatement",
    columnKeys: ["costAndExpenses"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "All costs incurred in the period.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyIncomeStatement}
          currency={info.row.original.financialData.companyIncomeStatement?.data?.reportedCurrency}
          onClick={() => {
            const fiscalPeriod = getIncomeStatementFiscalPeriod(info.row.original)
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} reported in ${fiscalPeriod} about Cost & Expenses?`,
              value: info.row.original.financialData.companyIncomeStatement?.data?.costAndExpenses,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyIncomeStatement?.data?.costAndExpenses}
        />
      )
    },
  })
}

export { costAndExpenses }
